.TodoEditor {
    width: 600px;
    display: flex;
    flex-direction: column;
    padding: 16px;
    border: 1px solid #2a2a2a;
    border-radius: 4px;
    margin-bottom: 20px;
  }
  
  .TodoEditor__textarea {
    height: calc(16px * 3 + 24px);
    padding: 12px 16px;
    border: 1px solid rgba(33, 33, 33, 0.2);
    border-radius: 4px;
  
    font: inherit;
    line-height: 16px;
    letter-spacing: 0.01em;
  
    resize: none;
    margin-bottom: 12px;
  }
  
  .TodoEditor__button {
    border: none;
    font: inherit;
    cursor: pointer;
    outline: none;
    width: 100%;
    margin-left: auto;
    border-radius: 4px;
    padding: 12px 24px;
    background-color: #3f51b5;
    color: #fff;
    font-weight: 500;
  }
  
  .TodoEditor__button:hover,
  .TodoEditor__button:hover:focus {
    background-color: #303f9f;
  }
  
  .TodoEditor__button:active {
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  }